import React from 'react'
import "./InfoBox.css"

function InfoBox({ image, link, text, title }) {
    return (
        <div className="infoBox">
            <div className="infoBox__image">
                <a href={link}><img src={image} alt=""></img></a>
            </div>
            <div className="infoBox__text">
                <a href={link}><h2>{title}</h2></a>
                <p>
                    {text}
                </p>
            </div>
        </div>
    )
}

export default InfoBox
